@import "~@/../scss/style.scss";





























































































































































































































































































































































































































.crop-range {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    overflow-y: hidden;
    .content {
        background-color: #fff;
        width: 100%;
        height: 100%;
        overflow-y: scroll;
        overflow: hidden;
        display: flex;
        flex-direction: column;

        //  1.標題及圖像說明
        .content-desc {
            margin: 0px 10px;
            .title {
                font-size: 16px;
                border-left: 5px solid #2d8cf0;
                padding-left: 10px;
                margin-bottom: 16px;
            }
            .desc {
                margin-left: 18px;
            }
        }
        // 2.圖像區域
        .content-image {
            display: flex;
            justify-content: rows;
            li {
                width: 180px;
                height: 180px;
                position: relative;
                .delete-img {
                    display: none;
                }
                &:hover {
                    .delete-img {
                        display: block;
                        position: absolute;
                        width: 180px;
                        height: 40px;
                        line-height: 40px;
                        left: 0px;
                        top: 140px;
                        background: rgba(59, 60, 61, 0.5);
                        // box-sizing: content-box;
                        z-index: 999;
                        cursor: pointer;
                        text-align: right;
                        i {
                            margin: 8px 10px 0 0;
                            display: block;
                            font-size: 24px;
                            color: white;
                        }
                    }
                }
            }
            img {
                width: 180px;
                height: 180px;
                border-radius: 4px;
            }
            .upload-img:hover {
                border: 1px dashed #92cccc;
            }
        }
    }
    // 裁剪圖像對話框

    .img-crop {
        // 1.截圖區域
        .imgCrop-content {
            display: flex;
            display: -webkit-flex;
            justify-content: flex-start;
            -webkit-justify-content: flex-start;
            .cropper-content {
                width: 43.075rem;
                height: 26.125rem;
                // margin-right: 40px;
            }
            .preview-content {
                .show-preview {
                    // border: 1px solid #ccc;
                    box-sizing: border-box;
                    width: 150px;
                    height: 150px;
                    overflow: hidden;
                    flex: 1;
                    -webkit-flex: 1;
                    display: flex;
                    display: -webkit-flex;
                    justify-content: center;
                    -webkit-justify-content: center;
                    .preview {
                        overflow: hidden;
                        /*border-radius: 50%;*/
                        /*border: 1px solid #cccccc;*/
                        background: #fff;
                        margin-left: 0px;
                    }
                }
                .desc {
                    margin-top: 10px;
                    text-align: center;
                }
                .el-button {
                    margin: 10px 40px;
                }
            }
        }
        // 2.操作按鈕區域
        .btn-content {
            width: 40rem;
            // margin: 10px 20px;
            text-align: center;
            margin: 1rem 2rem;
            // #uploads {
            //   position: absolute;
            //   clip: rect(0 0 0 0);
            // }
            .btn {
                // height: 2rem;
                // width: 2rem;
                // font-size: 1.5rem;
                margin: 3px 5px;
                // background-color: #fff;
                border: 1px solid #999;
                // border-radius: 4px;
                padding-top: 8px;
            }
        }
    }

    .range-container {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 3rem 2rem;
    }
    .custom-range {
        width: 16.25rem;
    }

    .btn-plus-minus {
        display: flex;
        margin-left: 1rem;
    }

    .btn-rotate {
        display: flex;
        position: absolute;
        cursor: pointer;
        right: -0.5rem;
    }

    .proportion {
        display: flex;
        position: absolute;
        width: 6rem;
        border: 1px solid #efefef;
        right: 0.8rem;
        bottom: 1rem;
        border-radius: 0.5rem;
        box-shadow: 0px 3px 10px #00000029;
        cursor: pointer;

        .hover-row {
            text-align: start;
            padding: 0.2rem 0.5rem;
            border-radius: 0.5rem;

            &.first {
                &:hover {
                    border-radius: 0.5rem 0.5rem 0 0;
                    background-color: #dadcde;
                }
            }

            &.middle {
                &:hover {
                    background-color: #dadcde;
                    border-radius: 0 0 0 0;
                }
            }

            &.last {
                &:hover {
                    border-radius: 0 0 0.5rem 0.5rem;
                    background-color: #dadcde;
                }
            }
        }
    }

    .button-bottom {
        margin-bottom: 3.75rem;
    }
}
