@import "~@/../scss/style.scss";







































































































































































































































































































































































































































































.selectd-border {
    border-radius: 8px;
    border: 3px solid #537895;
}

.swiper {
    height: 130px;
    width: 100%;

    .swiper-slide {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    img {
        width: 130px;
        height: 130px;
        cursor: pointer;
        object-fit: contain;
        opacity: 0.7;

        &:hover {
            opacity: 1;
        }
    }

    .swiper-button-prev:after {
        font-size: 1rem;
        font-weight: bold;
        margin-left: -1rem;
        background-color: #dadcde;
        padding: 0.5rem;
    }

    .swiper-button-next:after {
        font-size: 1rem;
        font-weight: bold;
        margin-right: -1rem;
        background-color: #cdd9e4;
        padding: 0.5rem;
    }
}

.plus-button {
    width: 130px;
    height: 130px;
    border: none;
    font-size: 3rem;
}

.swiper-box {
    width: 450px;
    height: auto;
    margin-top: 1rem;
}
